/* Styles pour le formulaire */
.form-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
}

.form-card {
    background-color: #f9f9f9;
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.form-section {
    margin-bottom: 20px;
}

.form-group {
    margin-bottom: 15px;
}

.form-group label {
    display: block;
    font-weight: bold;
}

.form-group input[type="text"],
.form-group select,
.form-group input[type="date"],
.form-group input[type="file"] {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.form-group select[multiple] {
    height: 100px; /* Ajustez la hauteur selon le nombre d'options */
}

.form-group small {
    display: block;
    color: #888;
    margin-top: 5px;
}

.form-actions {
    text-align: right;
}

/* Styles pour les boutons */
.btn {
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.btn-primary {
    background-color: #007bff;
    color: #fff;
}

.btn-gray {
    background-color: #ccc;
    color: #333;
    margin-right: 10px;
}

.btn:hover {
    opacity: 0.8;
}
